import React, { Component } from 'react';
import LeaderboardTitle from './LeaderboardTitle';
import LevelLeaderboardCard from './LevelLeaderboardCard';
import LevelLeaderboard from './LevelLeaderboard';

export class LevelLeaderboardsComponent extends Component {
  state = {
    showLevelLeaderboard: null,
    levelLeaderboardsArray: []
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.levelLeaderboards !== this.state.levelLeaderboardsArray) {

      let lvlLeaderboardsArr = [];
      for (let player in nextProps.levelLeaderboards) {
        lvlLeaderboardsArr.push(nextProps.levelLeaderboards[player]) 
      }    

      this.setState(prevState => {
        return {
          ...prevState,
          levelLeaderboardsArray: lvlLeaderboardsArr
        }
      })
    }
  }

  handleClick = (level) => {
    this.setState({ showLevelLeaderboard: level })
  }

  genLevelComponents = () => {
    let levels = Object.keys(this.props.levelLeaderboards);

    let levelComponents = levels.map((level, index) =>
      <LevelLeaderboardCard key={index} index={index} handleClick={this.handleClick} levelNumber={level-1} />
    );
    return <div className="d-flex w-100 justify-content-around flex-wrap" style={{ padding: '10px' }}>
      {levelComponents}
    </div>;
  }

  render() {
    return (
      <div style={Leaderboard} className='d-flex flex-column align-items-center'>
        <LeaderboardTitle text='Levels Leaderboard' />
        {this.state.showLevelLeaderboard !== null ? <LevelLeaderboard level={this.state.showLevelLeaderboard} levelsArr={this.state.levelLeaderboardsArray} handleClick={this.handleClick}/> : this.genLevelComponents()}
      </div>
    )
  }
}

const Leaderboard = {
  backgroundColor: 'rgba(15,50,85,0.7)',
  flex: 7,
  minHeight: '500px',
  maxHeight: '1200px',
  overflowY: 'scroll',
  margin: '0 10px',
  borderRadius: '5px'
}

export default LevelLeaderboardsComponent
