import React, { Component } from 'react'
import AvgLeaderboard from './AvgLeaderboard'
import LevelLeaderboardsComponent from './LevelLeaderboardsComponent'

export class MainContent extends Component {
  state = {
    levelLeaderboards: [],
    avgLeaderboard: []
  }

  async componentDidMount() {
    console.log("fetching data...");

    let levelLeaderboards = {};
    let avgLeaderboard = [];

    await fetch('../../api/leaderboard') //---- PROD
    // await fetch('https://tangram-node.herokuapp.com/api/leaderboard')
      .then(response => response.json())
      .then(data => levelLeaderboards = data)

    await fetch('../../api/avgLeaderboard') //---- PROD
    // await fetch('https://tangram-node.herokuapp.com/api/avgLeaderboard')
      .then(response => response.json())
      .then(data => avgLeaderboard = data)


    this.setState({
      levelLeaderboards: levelLeaderboards,
      avgLeaderboard: avgLeaderboard,
    })
    console.log("fetching complete!");
  }

  render() {
    return (
      <div className='d-flex'>
        <AvgLeaderboard avgLeaderboard={this.state.avgLeaderboard} />
        <LevelLeaderboardsComponent levelLeaderboards={this.state.levelLeaderboards} />
      </div>
    )
  }
}

export default MainContent
