import React, { Component } from 'react';
import LeaderboardEntry from './LeaderboardEntry';
//level, levelsArr/ handleclick
export class LevelLeaderboard extends Component {

  genListItems(level, levelsArr) {
    let lvlLeaderboardArr = [];
    for (let player in levelsArr[level]) {
      lvlLeaderboardArr.push([player, levelsArr[level][player]])
    }

    lvlLeaderboardArr.sort((a, b) => a[1] - b[1]); //sort the levelleaderboardarray 

    let lvlLeaderboardListItems = lvlLeaderboardArr.map((player, index) =>
      <LeaderboardEntry key={index} position={index + 1} name={player[0]} time={player[1]} isFloat={false} />
    )

    return lvlLeaderboardListItems
  }

  render() {
    return (
      <div className='d-flex w-100' >
        <div style={{ marginLeft: '20px', flex: 1 }} className='d-flex flex-column align-items-center'>
          <p style={levelNo}>Level {this.props.level + 1}</p>
          <button className="backButton" onClick={() => this.props.handleClick(null)}>Back</button>
        </div>
        <div style={levelLeaderboard} className='d-flex justify-content-center'>
          <ul style={list}>
            {this.genListItems(this.props.level, this.props.levelsArr)}
          </ul>
        </div>
      </div>
    )
  }
}

const list = {
  width: '70%',
  margin: '30px 0'
}

const levelNo = {
  fontFamily: 'Rubik',
  color: '#aaa',
  margin: '5px 0',
  fontSize: '35px'
}

const levelLeaderboard = {
  flex: 6
}

export default LevelLeaderboard
